.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItem {
    padding: 10px;
    color: white;
    background-color:aquamarine;
    border-radius: 15px;
}

.dialogsItem .active{
    color: gold
}

.messages {
    padding: 10px;
}

.messages .message {
    color: white;
}

.NewMessageArea{
    display: grid;
    grid-template-columns: 9fr 1fr;
}

.messageArea{
    width: 100%;
    height: 100%;
    padding : 10px;
    border-radius: 15px;
    box-sizing: border-box;
}

.sendButton{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    padding : 10px;
}