.wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-items: center;
}

.title {
    color: rgb(171, 107, 187);
    font-size: 24px;
}

.title:hover {
    scale: 1.1;
}

.img {
    width: 100%;
    border-radius: 50%;
}

.img:hover {
    scale: 1.1;
}

a {
    text-decoration: none;
}